.header {
  position: fixed;
  z-index: 100;
  width: 100%;
  border-bottom: 1px solid #dbe4f5;
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(166,202,13,1) 100%);
  // background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(166,202,13,0.630987429151348) 100%);
  transition: 0.5s;

  &--shadow {
    transition: 0.5s;
    box-shadow: 0px 8px 15px -5px #000000;
  }

  @media (max-width: 500px) {
    min-height: 64px;
    margin-bottom: 0;
  }
}
